.header__logo {
  font-family: "raleway", "arial", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.header__subtitle {
  font-family: "raleway", "arial", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.header__button, .header__title {
  font-family: "raleway", "arial", sans-serif;
  font-weight: 800;
  font-style: normal;
}

.header__text, .header__item-link {
  font-family: "montserrat", "arial", sans-serif;
  font-weight: 300;
  font-style: normal;
}

.header {
  display: flex;
  flex-direction: column;
  width: 1920px;
  height: 1024px;
  border: 1px solid #000;
  box-sizing: border-box;
  background-image: url(../img/background-images/header-image.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #555556;
  background-blend-mode: multiply;
}
.header__top {
  border: 1px solid #ff0000;
  display: flex;
  margin: 30px 0 0 0;
  padding: 0 374px 0 376px;
}
.header__logo {
  border: 1px solid #f6ff00;
  box-sizing: border-box;
  display: flex;
  margin-top: 8px;
}
.header__logo-text {
  margin: 8px 0 15px -20px;
  font-size: 34px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.5px;
  text-align: right;
  color: #ffffff;
}
.header__nav {
  border: 1px solid #1d00f9;
  display: flex;
  margin-left: 185px;
  padding: 28px;
  min-width: 699px;
}
.header__list {
  display: flex;
  border: 1px solid #000;
  box-sizing: border-box;
  width: 100%;
  justify-content: space-between;
}
.header__item {
  border: 1px solid #e90707;
  display: flex;
}
.header__item-link {
  object-fit: contain;
  font-size: 14px;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
  border: 1px solid #ffe600;
  display: flex;
}
.header__basket-button {
  margin: 3px 22px 11px 31px;
  background: none;
}
.header__search-form {
  padding: 23px 0 10px 0;
}
.header__carousel {
  border: 1px solid #ff00bb;
  width: 1920px;
  min-height: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding: 0 150px 0 150px;
}
.header__carousel-textbox {
  border: 1px solid #000;
  min-height: 200px;
  width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 256px 0 118px 0;
  margin-bottom: 152px;
  color: #ffffff;
}
.header__paggination {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 30px;
  margin-top: 45px;
  border: 2px solid #ff0105;
}
.header__paggination-items {
  display: block;
  width: 10px;
  height: 10px;
  margin: 0 5px;
  border-radius: 50%;
  background-color: gold;
}
.header__arrow_left {
  box-sizing: border-box;
  border: 1px solid #9e0000;
  height: 80px;
  width: 45px;
  margin-top: 352px;
}
.header__arrow_right {
  border: 1px solid #0d01b9;
  height: 80px;
  width: 45px;
  margin-top: 352px;
}
.header__subtitle {
  background: #000;
  align-items: center;
  font-size: 36px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
}
.header__title {
  background: #ccc;
  margin: 0 auto;
  font-size: 52px;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
}
.header__text {
  background: #827676;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
}
.header__button {
  width: 170px;
  height: 53px;
  margin: 0 auto;
  background-color: #998675;
  border-radius: 3px;
  box-shadow: inset 0px -3px 0 0 #736357;
  color: #ffffff;
  font-size: 14px;
  font-weight: 800;
  font-stretch: normal;
  font-style: normal;
  line-height: 52px;
  letter-spacing: normal;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
.header__button:hover {
  background-color: #ffffff;
  color: #555555;
}
.header__button:active {
  background-color: #736357;
  color: #ffffff;
}
.header__button:visited {
  color: #fff;
}
.header input[type=search] {
  display: none;
}

